/* The Image container */
.img-hover-zoom--slowmo {
    overflow: hidden;
}

/* Slow-motion Zoom Container */
.img-hover-zoom--slowmo img {
    transition: transform 0.5s, filter 3s ease-in-out;
}

/* The Transformation */
.img-hover-zoom--slowmo:hover img {
    transform: scale(1.1);
}

